<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者:{{blog.author}}</p>
        <p>分类:</p>
        <ul>
            <li v-for="(category,idx) in blog.categories" :key="idx">
                {{category}}
            </li>
        </ul>
        <button @click="deleteSingleblog">删除</button>
        <router-link :to="'/edit/'+id">编辑</router-link>
    </div>
</template>
<script>
export default {
    name:'single-blog',
    data(){
        return{
            id:this.$route.params.id,
            blog:{

            }
        }
    },
    created(){
        this.$http.get("https://vue-demo-2e2df.firebaseio.com/posts/" + this.id + ".json")
        .then(function (res) {
            return res.json();
            console.log(res);
            // this.blog = res.data;
        })
        .then(function (data) {
            // console.log(data);
            this.blog = data;
        });
    },
    methods:{
        deleteSingleblog(){
            this.$http.delete("https://vue-demo-2e2df.firebaseio.com/posts/" + this.id + ".json")
            .then(res => {
                this.$router.push({path:'/'})
            });
        }
    }
}
</script>
<style scoped>
    #single-blog{
        max-width: 960px;
        margin:0 auto;
        padding:20px;
        background:#eee;
        border:1px dotted #aaa;
    }
</style>


