<template>
    <div v-theme:column="'narrow'" id="show-blogs">
        <h1>博客总览</h1>
        <input type="text" placeholder="搜索" v-model="search">
        <div v-for="(blog,idx) in filteredBlogs" :key="idx" class="single-blog">
            <router-link v-bind:to="'/blog/' + blog.id">
                <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
            </router-link>
            <article>
                {{blog.content | snippet}}
            </article>
        </div>
    </div>
</template>
<script>
export default {
    name:'show-blogs',
    data(){
        return{
            blogs:[],
            search:''
        }
    },
    created(){
        this.$http.get("https://vue-demo-2e2df.firebaseio.com/posts.json")
        .then(function (res) {
            return res.json();
            // this.blogs = res.body.slice(0,10);
            
        }).then(function (data) {
            var blogsArray = [];
            for(let key in data){
                // console.log(data[key]);
                data[key].id = key;
                blogsArray.push(data[key]);
            }
            // console.log(blogsArray);
            this.blogs = blogsArray;
        });
    },
    computed:{
        filteredBlogs:function(params) {
            return this.blogs.filter((blog) => {
                return blog.title.match(this.search);
            })
        }
    },
    filters:{
        toUppercase(value) {
            return value.toUpperCase();
        }
    }
}
</script>
<style scoped>
    #show-blogs{
        max-width: 800px;
        margin:0 auto;
    }
    .single-blog{
        padding:20px;
        margin:20px 0;
        box-sizing: border-box;
        background:#eee;
        border:1px dotted #aaa;
    }
    #show-blogs a{
        color: #444;
        text-decoration: none;
    }
    input[type="text"]{
        padding:8px;
        width:100%;
        box-sizing: border-box;
    }
</style>



