<template>
    <div id="edit-blog">
        <h1>编辑博客</h1>
        <form action="" v-if="!submmited">
            <label for="">博客标题</label>
            <input type="text" v-model="blog.title" required />

            <label for="">博客内容</label>
            <textarea name="" id="" v-model="blog.content"></textarea>

            <div id="checkboxs">
                <label for="">Vue.js</label>
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                <label for="">Node.js</label>
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                <label for="">React.js</label>
                <input type="checkbox" value="React.js" v-model="blog.categories">
                <label for="">Angular4</label>
                <input type="checkbox" value="Angular4" v-model="blog.categories">
            </div>
            <label for="">作者：</label>
            <select v-model="blog.author">
                <option v-for="(author,idx) in authors" :key="idx">
                    {{author}}
                </option>
            </select>
            <button @click.prevent="post">编辑博客</button>
        </form>
        <div v-if="submmited">
            <h3>您的博客发布成功！</h3>
        </div>
        <div id="preview">
            <h3>博客总览：</h3>
            <p>博客标题：{{blog.title}}</p>
            <p>博客内容：</p>
            <p>{{blog.content}}</p>
            <p>博客分类：</p>
            <ul>
                <li v-for="(category,idx) in blog.categories" :key="idx">
                    {{category}}
                </li>
            </ul>
            <p>作者：{{blog.author}}</p>

        </div>
    </div>
</template>
<script>
// https://jsonplaceholder.typicode.com/
// https://jsonplaceholder.typicode.com/posts
    export default {
        name:'edit-blog',
        data(){
            return{
                id:this.$route.params.id,
                blog:{
                    title:'',
                    content:'',
                    categories:[],
                    author:''
                },
                authors:['herry','jack','lucy'],
                submmited:false
            }
        },
        created(){
            this.fetchData();
        },
        methods:{
            fetchData(){
                // console.log(this.id);
                this.$http.get("https://vue-demo-2e2df.firebaseio.com/posts/" + this.id + ".json")
                .then(function (res) {
                    return res.json();
                    console.log(res);
                    // this.blog = res.data;
                })
                .then(function (data) {
                    // console.log(data);
                    this.blog = data;
                });
            },
            post:function(){
                console.log(123);
                this.$http.put("https://vue-demo-2e2df.firebaseio.com/posts/" + this.id + ".json",this.blog).then(function(res) {
                        console.log(res);
                        this.submmited = true;
                    });
            }
        }
    }
</script>
<style scoped>
    #add-blog *{
        box-sizing: border-box;
    }
    #add-blog{
        margin:20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label{
        display:block;
        margin:20px 0 10px;
    }
    input[type="text"],textarea,select{
        display: block;
        width: 100%;
        padding:8px;
    }
    textarea{
        height:200px;
    }
    #checkboxs label{
        display: inline-block;
        margin-top:0;
    }
    #checkboxs input{
        display: inline-block;
        margin-right:10px;
    }
    button{
        display: block;
        margin:20px 0;
        background:crimson;
        color: #fff;
        border:0;
        padding:14px;
        border-radius:4px;
        font-size:18px;
        cursor: pointer;
    }
    #preview{
        padding:10px 20px;
        border:1px dotted #ccc;
        margin:30px 0;
    }
    h3{
        margin-top:10px;
    }
</style>



